import { gql } from "apollo-boost";

export const GetPortfolioQuery = gql`
  query contentView_portfolio(
    $first: Int
    $skip: Int
    $where: PortfolioItemWhereInput
    $orderBy: PortfolioItemOrderByInput
  ) {
    contentView_portfolio: portfolioItemsConnection(
      first: $first
      skip: $skip
      where: $where
      orderBy: $orderBy
    ) {
      edges {
        node {
          status
          updatedAt
          createdAt
          id
          portfolioKey
          imageUrl {
            status
            updatedAt
            createdAt
            id
            handle
            fileName
            height
            width
            size
            mimeType
          }
          title
          subtitle
          content {
            status
            body {
              text
            }
            updatedAt
            createdAt
            id
            heading
            imageAfter {
              status
              updatedAt
              createdAt
              id
              handle
              fileName
              height
              width
              size
              mimeType
            }
            subheading
            bgHex {
              hex
              css
              rgba {
                r
                g
                b
                a
              }
            }
            textColor {
              hex
              css
              rgba {
                r
                g
                b
                a
              }
            }
            imageBefore {
              status
              updatedAt
              createdAt
              id
              handle
              fileName
              height
              width
              size
              mimeType
            }
          }
        }
      }
    }
  }
`;
