import React from "react";
import { AboutSectionWrapper } from "./AboutSection.styles";
import { Element } from "react-scroll";
import StickyNavWithHeader from "../Navigation/StickyNavWithHeader";
import AboutUsData from "../../data/AboutUsData";
const AboutSection = () => {
  return (
    <AboutSectionWrapper className="sectionWrapper">
      <StickyNavWithHeader
        color="darkNav"
        title="About"
        sectionAnchor="aboutSection"
      />
      <div className=" page">
        <div className="contentWrapper">
          <AboutUsData />
        </div>
      </div>
    </AboutSectionWrapper>
  );
};
export default AboutSection;
