import { Link } from "react-scroll";
import React from "react";
import { NavigationWrapper } from "./Navigation.styles";

const Navigation = props => {
  const { location } = props;
  return (
    <NavigationWrapper className={location && location}>
      <li>
        <Link to="aboutSection" spy={true} smooth={true} duration={500}>
          About
        </Link>
      </li>
      <li>
        <Link to="portfolioSection" spy={true} smooth={true} duration={500}>
          Work
        </Link>
      </li>
      <li>
        <Link to="contactSection" spy={true} smooth={true} duration={500}>
          Contact
        </Link>
      </li>
      <li>
        <a href="/JessicaSutherlandResume.pdf" target="_blank">
          Resume
          </a>
      </li>
    </NavigationWrapper>
  );
};

export default Navigation;
