import React from "react";
import Navigation from "../Navigation/Navigation";
import { HeaderHero } from "./LandingPageHeader.styles";

const LandingPageHeader = () => {
  return (
    <HeaderHero className="page">
      <div className="homeTitle">
        <h1> Jessica Sutherland </h1>
        <span>
          I’m a focused and passionate professional with over 10 years of
          marketing experience living in Charlotte, North Carolina.
        </span>

        <Navigation location="headerNav" />
      </div>
    </HeaderHero>
  );
};

export default LandingPageHeader;
