import styled from "styled-components";

const popColor1 = "#e0b312";
export const HeaderHero = styled.header`
  grid-column: 1 / 2;
  background: #eeeeee url("/BG.jpg") no-repeat;
  background-size: cover;
  background-position: bottom right;
  // background: #eee;
  color: #000;
  text-align: center;
  display: grid;
  // grid-template-columns: auto;
  // grid-template-rows: 25% auto;
  .homeTitle {
    display:flex;
    flex-direction:column;
    justify-content:center;
    text-align: left;
    margin-top: 160px;
    margin-left: 10.5%;
    color: black;
    span {
      width:700px
      display:block;
      // display: none;
      margin-left: 0px;
      font-size: 20px;
      // letter-spacing:1px;
      font-weight: 400;
      padding:0 20px;
      line-height:1.2;
      color:#666;
    }
    h1 {
      font-family: "Dianna Webber", cursive;
      color:${popColor1}
      font-size: 120px;
      margin-bottom:0px;
    }
  }

  .smallHeader {
    font-size: 16px;
    text-transform: uppercase;
    padding: 28px 0;
    span {
      margin: 0 10px;
    }
  }
`;
